import { Card, Image, Tag, Typography } from "antd";
import { useHistory } from "react-router-dom";
import React from "react";
import { IMovie } from "../../reducers/movieReducer";

export const notfound =
  "data:image/png;base64,";
const { Paragraph, Text } = Typography;

interface MovieCardProps extends IMovie {
  search: string;
}

const MovieCard = (props: MovieCardProps) => {
  const history = useHistory();

  return (
    <Card
      className="movie-card"
      cover={
        <Image
          alt={props.Title}
          loading="lazy"
          src={props.Poster}
          preview={false}
          height={300}
          fallback={notfound}
          className="object-fit-cover movie-poster"
          data-testid="movie-poster"
        />
      }
      size="small"
      bordered={false}
      onClick={() => history.push(`/${props.imdbID}/${props.search}`)}
    >
      <Text type="secondary" strong data-testid="movie-year">
        {props.Year}
      </Text>
      <Paragraph
        ellipsis={{ rows: 2 }}
        strong
        style={{ minHeight: 44 }}
        data-testid="movie-title"
      >
        {props.Title}
      </Paragraph>
      <Tag className="movie-type" color="#108ee9" data-testid="movie-type">
        {props.Type}
      </Tag>
    </Card>
  );
};

export default MovieCard;
